.datos{
    height: 90%;  
    width: 100%;
    display: grid;
    
    grid-template-rows:  auto;
    margin: auto;
    padding: 0 px;
   justify-content:flex-start;
    background-color: aquamarine;
   
}

.cajon{
    display: grid;
    height: 85%;
    width: 320px;
     margin-bottom: 10px;
     border-radius: 5px;
     border: 4cap rgb(31, 41, 50);
     background-color:aquamarine;
     font-size: small;
     font-style: initial;
   
   }
   .cajon1{
    font-family: 'Franklin Gothic Medium', 'Arial Narrow', Arial, sans-serif;
    font-style: oblique;
    font-size: larger;
    margin-left: 30px;
   }
   .cajon2{
    display: grid;
    height: 80%;
    width: 350px;
    
    border-radius: 8px;
    grid-template-columns: auto auto;
    background-color: rgb(237, 93, 93);
   }
   .cajon2.temp{ 
    display: grid;
    grid-template-columns: 1fr 1fr;    
    
   }
   .temp1{ width: 150px;
    margin: 12px;
    border-radius: 5px;
    border: 4cap rgb(86, 99, 112);
    background-color: rgb(194, 196, 184);
    font-size: small;
    font-style: initial;

   }
